'use client';
import { useEffect, useState } from 'react';

export default function ProjectsPage() {
    const [repos, setRepos] = useState([]);
    const [loading, setLoading] = useState(true);

    // Manual projects for the top of the list
    const staticProjects = [
        {
            name: "Conversational AI (ROHbot)",
            description: "Full-stack Agentic RAG system with Gemini & Supabase integration for personalized AI interactions.",
            html_url: "https://github.com/RohanrajeBHosale/ROHbot",
            language: "JavaScript",
        }
    ];

    useEffect(() => {
        const GITHUB_USERNAME = "RohanrajeBHosale";
        const fetchRepos = async () => {
            try {
                const response = await fetch(
                    `https://api.github.com/search/repositories?q=user:${GITHUB_USERNAME}+topic:portfolio&sort=updated`
                );
                const data = await response.json();
                const githubItems = data.items || [];

                const filteredStatic = staticProjects.filter(
                    sp => !githubItems.some(gh => gh.name.toLowerCase() === sp.name.toLowerCase())
                );

                setRepos([...filteredStatic, ...githubItems]);
                setLoading(false);
            } catch (error) {
                setRepos(staticProjects);
                setLoading(false);
            }
        };
        fetchRepos();
    }, []);

    // --- Inline Styling (Premium Dark Mode) ---
    const s = {
        container: { maxWidth: '900px', margin: '0 auto', padding: '60px 20px', fontFamily: 'sans-serif' },
        header: { marginBottom: '60px' },
        h1: { fontSize: '4rem', fontWeight: '800', margin: '0 0 10px 0', letterSpacing: '-2px', color: '#fff' },
        subtitle: { color: '#666', fontSize: '1.2rem', lineHeight: '1.6', maxWidth: '600px' },
        grid: { display: 'flex', flexDirection: 'column', gap: '20px' },
        card: {
            backgroundColor: '#0a0a0a',
            border: '1px solid #1a1a1a',
            borderRadius: '16px',
            padding: '35px',
            textDecoration: 'none',
            color: 'inherit',
            transition: '0.2s ease'
        },
        cardHeader: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '15px' },
        title: { fontSize: '1.6rem', fontWeight: '700', color: '#eee', margin: 0 },
        lang: { fontSize: '10px', fontWeight: '800', padding: '5px 10px', backgroundColor: '#111', border: '1px solid #222', borderRadius: '6px', color: '#555', textTransform: 'uppercase' },
        desc: { color: '#888', fontSize: '1rem', lineHeight: '1.6', margin: '0 0 30px 0' },
        footer: { display: 'flex', alignItems: 'center', gap: '8px', color: '#3b82f6', fontSize: '13px', fontWeight: '700', textTransform: 'uppercase', letterSpacing: '0.5px' }
    };

    return (
        <div style={s.container}>
            <header style={s.header}>
                <h1 style={s.h1}>Projects.</h1>
                <p style={s.subtitle}>
                    An automated showcase of my AI agents, data engineering pipelines, and specialized research.
                </p>
            </header>

            {loading ? (
                <div style={{ color: '#222', fontWeight: '900', letterSpacing: '2px' }}>SYNCING_GITHUB_DATABASE...</div>
            ) : (
                <div style={s.grid}>
                    {repos.map((repo, idx) => (
                        <a
                            key={idx}
                            href={repo.html_url}
                            target="_blank"
                            rel="noreferrer"
                            style={s.card}
                            onMouseOver={(e) => {
                                e.currentTarget.style.borderColor = '#333';
                                e.currentTarget.style.backgroundColor = '#0f0f0f';
                                e.currentTarget.style.transform = 'translateY(-2px)';
                            }}
                            onMouseOut={(e) => {
                                e.currentTarget.style.borderColor = '#1a1a1a';
                                e.currentTarget.style.backgroundColor = '#0a0a0a';
                                e.currentTarget.style.transform = 'translateY(0)';
                            }}
                        >
                            <div style={s.cardHeader}>
                                <h3 style={s.title}>
                                    {repo.name.replace(/_/g, ' ').replace(/-/g, ' ')}
                                </h3>
                                <span style={s.lang}>{repo.language || 'AI'}</span>
                            </div>

                            <p style={s.desc}>
                                {repo.description || "Building intelligent AI systems and robust data architectures."}
                            </p>

                            <div style={s.footer}>
                                View Repository
                                <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg>
                            </div>
                        </a>
                    ))}
                </div>
            )}
        </div>
    );
}